import React, { useCallback, useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { useTheme } from '@mui/material/styles';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { FormControl, InputLabel, MenuItem, Select, TextField } from '@mui/material';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ReportIcon from '@mui/icons-material/Report';
import { ViewContext } from '../contexts/viewContext';

function TabPanel(props) {
	const { children, value, index, ...other } = props;

	return (
		<Typography
			component='div'
			role='tabpanel'
			hidden={value !== index}
			id={`action-tabpanel-${index}`}
			aria-labelledby={`action-tab-${index}`}
			{...other}
		>
			{value === index && <Box sx={{ p: 3 }}>{children}</Box>}
		</Typography>
	);
}

TabPanel.propTypes = {
	children: PropTypes.node,
	index: PropTypes.number.isRequired,
	value: PropTypes.number.isRequired,
};

function a11yProps(index) {
	return {
		id: `action-tab-${index}`,
		'aria-controls': `action-tabpanel-${index}`,
	};
}

export default function Screening() {
	const theme = useTheme();
	const [value, setValue] = useState(0);
	const [answers, setAnswers] = useState({
		ontario: '',
		legalStatus: '',
		sourceOfIncome: '',
		onlineBanking: '',
		email: '',
		name: '',
	});

	const view = useContext(ViewContext);

	useEffect(() => {
		console.log(view);
	}, [view]);

	const [errors, setErrors] = useState({});

	const [result, setResult] = useState('red'); // green, yellow red

	const handleChange = (event, newValue) => {
		setValue(newValue);
	};

	const handleChangeIndex = index => {
		setValue(index);
	};

	const handleStart = () => {
		const validation = confirmName();
		if (validation.valid) {
			setValue(value => value + 1);
			setErrors({});
		} else {
			setErrors(validation.errors);
		}
	};

	const handleEnterNext = key => {
		if (key.code === 'Enter') handleContinue();
	};

	const handleContinue = () => {
		setValue(value => value + 1);
	};

	const handleOntario = answer => {
		setAnswers(prev => ({ ...prev, ontario: answer }));
		setValue(value => value + 1);
	};
	const handleLegalStatus = e => {
		setAnswers(prev => ({ ...prev, legalStatus: e.target.value }));
	};
	const handleSourceOfIncome = answer => {
		setAnswers(prev => ({ ...prev, sourceOfIncome: answer }));
		setValue(value => value + 1);
	};
	const handleOnlineBanking = answer => {
		setAnswers(prev => ({ ...prev, onlineBanking: answer }));
		setValue(value => value + 1);
	};

	const checkAnswers = useCallback(a => {
		if (a.ontario === 'NO') return 'red';
		if (a.ontario === 'UNSURE') return 'yellow';
		if (a.legalStatus === 'UNSURE' || a.legalStatus === 'OTHER') return 'yellow';
		if (a.sourceOfIncome === 'UNSURE') return 'yellow';
		if (a.sourceOfIncome === 'NO') return 'red';
		if (a.onlineBanking === 'UNSURE') return 'yellow';
		if (a.onlineBanking === 'NO') return 'red';
		return 'green';
	}, []);

	useEffect(() => {
		setResult(checkAnswers(answers));
	}, [answers, checkAnswers]);

	const confirmName = () => {
		const validation = {
			valid: true,
			errors: {},
		};
		if (answers.name === '') {
			validation.valid = false;
			validation.errors = { ...validation.errors, name: 'Full name field is required' };
		}
		if (answers.email === '') {
			validation.valid = false;
			validation.errors = { ...validation.errors, email: 'Please enter a Valid Email' };
		}

		return validation;
	};

	const handleGreenClick = useCallback(() => {
		console.log(view.greenUrl);
	}, [view]);
	const handleYellowClick = useCallback(() => {
		console.log(view.yellowUrl);
	}, [view]);
	const handleRedClick = useCallback(() => {
		console.log(view.redUrl);
	}, [view]);

	return (
		<div>
			<Tabs
				value={value}
				onChange={handleChange}
				indicatorColor='primary'
				textColor='primary'
				variant='fullWidth'
				aria-label='action tabs example'
			>
				<Tab label='' disabled {...a11yProps(0)} />
				<Tab label='' disabled {...a11yProps(1)} />
				<Tab label='' disabled {...a11yProps(2)} />
				<Tab label='' disabled {...a11yProps(3)} />
				<Tab label='' disabled {...a11yProps(4)} />
				<Tab label='' disabled {...a11yProps(5)} />
			</Tabs>

			<SwipeableViews
				axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
				index={value}
				onChangeIndex={handleChangeIndex}
			>
				<TabPanel value={value} index={0} dir={theme.direction}>
					<div className='text-center text-3xl font-bold my-16 w-[70%] mx-auto'>
						<div>Let's start with your name</div>
						<div className='my-4'>
							<TextField
								name='name'
								value={answers.name}
								onChange={e => setAnswers(prev => ({ ...prev, name: e.target.value }))}
								variant='outlined'
								fullWidth
								label='FULL NAME'
								{...(errors?.name && { error: true, helperText: errors?.name })}
							/>
						</div>
						<div>
							<TextField
								name='email'
								value={answers.email}
								onChange={e => setAnswers(prev => ({ ...prev, email: e.target.value }))}
								variant='outlined'
								fullWidth
								label='EMAIL'
								{...(errors?.email && { error: true, helperText: errors?.email })}
								onKeyDown={handleEnterNext}
							/>
						</div>
					</div>
					<div className='flex items-center justify-center gap-8'>
						<div onClick={handleStart} className='bg-[#90FFBE] p-4 rounded-full w-32 text-center cursor-pointer'>
							START
						</div>
					</div>
				</TabPanel>
				<TabPanel value={value} index={1} dir={theme.direction}>
					<div className='text-center text-3xl font-bold my-16'>
						Are you currently in the Province of Ontario, Canada?
					</div>
					<div className='flex items-center justify-center gap-8 mb-8'>
						<div
							onClick={() => handleOntario('YES')}
							className='bg-[#90FFBE] p-4 rounded-full w-32 text-center cursor-pointer'
						>
							YES
						</div>
						<div
							onClick={() => handleOntario('NO')}
							className='bg-[#86CDFF] p-4 rounded-full w-32 text-center cursor-pointer text-white'
						>
							NO
						</div>
						<div
							onClick={() => handleOntario('UNSURE')}
							className='bg-[#606060] p-4 rounded-full w-32 text-center text-white cursor-pointer'
						>
							UNSURE
						</div>
					</div>
					<div
						onClick={() => setValue(value => value - 1)}
						className='text-gray-400 uppercase text-xs mx-auto w-14 cursor-pointer'
					>
						go back
					</div>
				</TabPanel>
				<TabPanel value={value} index={2} dir={theme.direction}>
					<div className='text-center text-3xl font-bold my-16'>
						Are you currently in the Province of Ontario, Canada?
					</div>
					<div className='mb-8'>
						<FormControl fullWidth>
							<InputLabel id='demo-simple-select-label'>Legal Status</InputLabel>
							<Select
								labelId='demo-simple-select-label'
								id='demo-simple-select'
								value={answers.legalStatus}
								label='Legal Status'
								onChange={handleLegalStatus}
								onKeyDown={handleEnterNext}
							>
								<MenuItem value={''}>Select one</MenuItem>
								<MenuItem value={'CITIZEN'}>Citizen</MenuItem>
								<MenuItem value={'PERMANENT'}>Permanent Resident</MenuItem>
								<MenuItem value={'WORK-PERMIT'}>Foreign Worker (Work Permit Holder)</MenuItem>
								<MenuItem value={'STUDENT'}>Student (Study Permit Holder)</MenuItem>
								<MenuItem
									value={'REFUGEE'}
									style={{
										whiteSpace: 'unset',
										wordBreak: 'break-all',
									}}
								>
									Refugee (Refugee Protection Claimant Document or Resolution from the Immigration and Refugee
									Board of Canada granting Refugee Status Holder)
								</MenuItem>
								<MenuItem value={'OTHER'}>Other</MenuItem>
								<MenuItem value={'UNSURE'}>Unsure</MenuItem>
							</Select>
						</FormControl>
					</div>
					<div className='flex items-center justify-center gap-8'>
						<div
							onClick={handleContinue}
							className='bg-[#90FFBE] p-4 rounded-full w-32 text-center cursor-pointer mb-4'
						>
							CONTINUE
						</div>
					</div>
					<div
						onClick={() => setValue(value => value - 1)}
						className='text-gray-400 uppercase text-xs mx-auto w-14 cursor-pointer'
					>
						go back
					</div>
				</TabPanel>
				<TabPanel value={value} index={3} dir={theme.direction}>
					<div className='text-center text-3xl font-bold my-16'>Do you have a verifiable source of income?</div>
					<div className='flex items-center justify-center gap-8 mb-8'>
						<div
							onClick={() => handleSourceOfIncome('YES')}
							className='bg-[#90FFBE] p-4 rounded-full w-32 text-center cursor-pointer'
						>
							YES
						</div>
						<div
							onClick={() => handleSourceOfIncome('NO')}
							className='bg-[#86CDFF] p-4 rounded-full w-32 text-center cursor-pointer text-white'
						>
							NO
						</div>
						<div
							onClick={() => handleSourceOfIncome('UNSURE')}
							className='bg-[#606060] p-4 rounded-full w-32 text-center text-white cursor-pointer'
						>
							UNSURE
						</div>
					</div>
					<div
						onClick={() => setValue(value => value - 1)}
						className='text-gray-400 uppercase text-xs mx-auto w-14 cursor-pointer'
					>
						go back
					</div>
				</TabPanel>
				<TabPanel value={value} index={4} dir={theme.direction}>
					<div className='text-center text-3xl font-bold my-16'>
						Do you have an Online Banking enabled account with a Bank, Credit Union, or other, to your name?
					</div>
					<div className='flex items-center justify-center gap-8 mb-8'>
						<div
							onClick={() => handleOnlineBanking('YES')}
							className='bg-[#90FFBE] p-4 rounded-full w-32 text-center cursor-pointer'
						>
							YES
						</div>
						<div
							onClick={() => handleOnlineBanking('NO')}
							className='bg-[#86CDFF] p-4 rounded-full w-32 text-center cursor-pointer text-white'
						>
							NO
						</div>
						<div
							onClick={() => handleOnlineBanking('UNSURE')}
							className='bg-[#606060] p-4 rounded-full w-32 text-center text-white cursor-pointer'
						>
							UNSURE
						</div>
					</div>
					<div
						onClick={() => setValue(value => value - 1)}
						className='text-gray-400 uppercase text-xs mx-auto w-14 cursor-pointer'
					>
						go back
					</div>
				</TabPanel>
				<TabPanel value={value} index={5} dir={theme.direction}>
					<div>
						{result === 'green' ? (
							<div className='text-center my-16 flex items-center flex-col'>
								<div className='text-2xl font-bold'>
									<CheckCircleIcon className='text-green-400' fontSize='large' />
								</div>
								<div className='text-2xl font-bold'>You are eligible to apply for our products!</div>
								<div>First you will need to sign up for and account on our app. Just click the button</div>
								<div
									onClick={handleGreenClick}
									className='bg-gradient-to-r from-[#90FFBE] to-[#86CDFF] hover:text-white my-4 p-4 rounded-full w-32 text-center cursor-pointer shadow-lg hover:bg-black '
								>
									SIGNUP
								</div>
							</div>
						) : result === 'yellow' ? (
							<div className='text-center my-16 flex items-center flex-col'>
								<div className='text-2xl font-bold'>
									<ReportIcon className='text-yellow-400' fontSize='large' />
								</div>
								<div className='text-2xl font-bold'>
									At this time, we can't asses your eligibility without obtaining further information.
								</div>
								<div>Would you like to schedule a phone call with a member of our team?</div>
								<div
									onClick={handleYellowClick}
									className='bg-gradient-to-r from-[#90FFBE] to-[#86CDFF] hover:text-white my-4 p-4 rounded-full w-64 uppercase text-center cursor-pointer shadow-lg hover:bg-black '
								>
									Schedule a call
								</div>
							</div>
						) : (
							<div className='text-center my-16 flex items-center flex-col'>
								<div className='text-2xl font-bold'>
									<ReportIcon className='text-yellow-400' fontSize='large' />
								</div>
								<div className='text-2xl font-bold'>
									Unfortunately, based on your answers you are not eligible for our products.
								</div>
								<div>
									Please keep us in mind if your circumstances change and we will gladly consider you in the
									future
								</div>
								<div
									onClick={handleRedClick}
									className='bg-gradient-to-r from-[#90FFBE] to-[#86CDFF] hover:text-white my-4 py-4 px-8 rounded-full text-[#606060] uppercase text-center cursor-pointer shadow-lg hover:bg-black '
								>
									Subscribe to our Financial Education Content
								</div>
							</div>
						)}
					</div>
					<div
						onClick={() => setValue(value => value - 1)}
						className='text-gray-400 uppercase text-xs mx-auto w-14 cursor-pointer'
					>
						go back
					</div>
				</TabPanel>
			</SwipeableViews>
		</div>
	);
}
