import { Slider } from '@mui/material';
import React, { useCallback, useEffect, useState } from 'react';

const products = {
	monthly: {
		landed1: {
			fee: 49.99,
			interest: 0.00283282724670457,
			factor: 0.09666667,
		},
		landed2: {
			fee: 53.99,
			interest: 0.00408837640517603,
			factor: 0.061666,
		},
		settled: {
			fee: 55.99,
			interest: 0.00229922176380833,
			factor: 0.028186,
		},
	},
	biweekly: {
		landed1: {
			fee: 21.99,
			interest: 0.00283282724670457,
			factor: 0.04437391,
		},
		landed2: {
			fee: 22.99,
			interest: 0.00408837640517603,
			factor: 0.02829548,
		},
		settled: {
			fee: 26.99,
			interest: 0.00229922176380833,
			factor: 0.01297345,
		},
	},
};

const initialDurations = [
	{
		label: '1 Year',
		value: 'landed1',
	},
	{
		label: '2 Years',
		value: 'landed2',
	},
	{
		label: '5 Years',
		value: 'settled',
	},
];

function Calculator() {
	const [instalment, setInstalment] = useState(0);
	const [frequency, setFrequency] = useState('biweekly');
	const [duration, setDuration] = useState('settled');
	const [amount, setAmount] = useState(5000);
	const [productDurations, setProductDurations] = useState(initialDurations);

	useEffect(() => {
		if (amount > 0) {
			const info = products[frequency][duration];

			if (amount < 2000 && duration !== 'landed1') {
				setDuration('landed1');
				setProductDurations([
					{
						label: '1 Year',
						value: 'landed1',
					},
				]);
			} else if (amount < 3000 && amount >= 2000) {
				if (duration === 'settled') setDuration('landed2');
				setProductDurations([
					{
						label: '1 Year',
						value: 'landed1',
					},
					{
						label: '2 Years',
						value: 'landed2',
					},
				]);
			} else if (amount < 4000 && amount >= 3000) {
				setDuration('landed2');
				setProductDurations([
					{
						label: '2 Years',
						value: 'landed2',
					},
				]);
			} else if (amount < 5000 && amount >= 4000) {
				if (duration === 'landed1') setDuration('settled');
				setProductDurations([
					{
						label: '2 Years',
						value: 'landed2',
					},
					{
						label: '5 Years',
						value: 'settled',
					},
				]);
			} else if (amount >= 5000) {
				setDuration('settled');
				setProductDurations([
					{
						label: '5 Years',
						value: 'settled',
					},
				]);
			}

			const tempInstalment = amount * info.factor + info.fee;
			setInstalment(tempInstalment);
		}
	}, [amount, frequency, duration]);

	const handleFrequencySelect = useCallback(e => {
		setFrequency(e.target.value);
	}, []);

	const handleDurationSelect = useCallback(e => {
		setDuration(e.target.value);
	}, []);

	const sliderFormat = (value, index) => {
		return `$ ${value}`;
	};

	const handleApply = () => {};

	return (
		<div className='p-4'>
			<div className='grid grid-cols-1 md:grid-cols-3 gap-4 shadow-xl rounded-t-3xl rounded-b-3xl md:rounded-r-3xl'>
				<div className='md:flex h-full'>
					{/* Aqui tengo los dos divs, que en movil esta uno debajo del otro y en md uno al lado del otro */}
					<div className='bg-gradient-to-b md:bg-gradient-to-r from-[#90FFBE] to-[#86CDFF]  flex items-center justify-center flex-col flex-auto pt-8 md:pt-0 rounded-t-3xl md:rounded-l-3xl md:rounded-tl-3xl md:rounded-t-none'>
						<div className='text-[#112446]'>
							{frequency === 'biweekly' ? 'Bi-Weekly' : 'Monthly'} instalments of:
						</div>{' '}
						<div className='text-[#112446] text-5xl font-extrabold mt-2'>$ {instalment.toFixed(2)}</div>
						<div
							className='w-[50%] bg-black text-white rounded-full text-sm text-center p-2 shadow-xl mt-8 cursor-pointer'
							onClick={handleApply}
						>
							APPLY NOW!
						</div>
					</div>
					{/* en este cambio el estilo para que sea arriba o abajo el border */}
					<div className='bg-[#86CDFF] w-40 h-10 md:h-full down-arrow md:right-arrow flex-1'></div>
				</div>
				<div className='md:col-span-2 p-6'>
					<div className='text-2xl my-4'>
						You will receive: <span className='text-4xl font-bold'>$ {amount}</span>
					</div>
					<div className='flex my-4' style={{ width: '90%' }}>
						<div className='flex-auto'>Min:$ 1.500</div>
						<div>Max:$ 10.000</div>
					</div>
					<div>
						<Slider
							valueLabelDisplay='auto'
							valueLabelFormat={sliderFormat}
							aria-label='pretto slider'
							value={amount}
							min={1500}
							max={10000}
							style={{ width: '90%' }}
							onChange={e => setAmount(e.target.value)}
						/>
					</div>
					<div>
						<span>Paid:</span>
						<select onChange={handleFrequencySelect} value={frequency}>
							<option value='monthly'>Monthly</option>
							<option value='biweekly'>Bi-Weekly</option>
						</select>
					</div>
					<div>
						<span>For:</span>
						<select onChange={handleDurationSelect} value={duration}>
							{productDurations.map((d, idx) => (
								<option key={idx} value={d.value}>
									{d.label}
								</option>
							))}
						</select>
					</div>
					<div className='text-xs my-2'>*Each case must be analyzed to determine the final amounts. </div>
				</div>
			</div>

			<div className='md:col-span-3'>
				<div className='border-[#606060] border-2 border-dashed border-t-0 w-[90%] mx-auto p-2 text-center rounded-b-2xl'>
					<span className='text-xs uppercase underline text-[#606060] cursor-pointer'>
						See the full payment schedule
					</span>
				</div>
				{/* <div id='gaveta-grande'>Gaveta Grande</div> */}
			</div>
		</div>
	);
}

export default Calculator;
